:root {
  --link-color: #00acdc;
}

body {
  justify-content: center;
  align-self: center;
  align-items: center;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.photo--logo {
  margin-top: 10px;
}

.nav {
  height: 60px;
}

.nav__list {
  margin-top: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--link-color);
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

